.app-logo {
  -webkit-animation: app-logo-spin infinite 60s linear;
          animation: app-logo-spin infinite 60s linear;
  height: 60px;
  vertical-align: middle;
}

.app-header {
  white-space: nowrap;
  font-size: 22px;
  background-color: #222;
  height: 90px;
  color: #fff;
  line-height: 90px;
}

.app-header .info {
  display: inline-block;
  margin-left: 40px;
  vertical-align: middle;
  color: #aaa;
  font-size: 12px;
  line-height: normal;
}

.app-header a {
  float: right;
  margin-right: 24px;
}

.app-content {
  margin: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.app-content .title {
  margin: 6px 0 6px 0;
  color: #a0522d;
  font-weight: bold;
}

.app-content .title a {
  text-decoration: none;
  color: #d78025;
}
.app-content .title a:hover {
  color: #a0522d;
}

.cp, .sketch-picker {
  margin: 6px 0 6px 0;
}

.palette {
  border: 1px solid #ccc;
}

.result {
  height: 72px;
  width: 460px;
  margin-bottom: 8px;
  padding: 4px;
  background-color: #eee;
  overflow-y: scroll;
  font-family: monospace;
  font-size: 0.8em;
}

@-webkit-keyframes app-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes app-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



